.plans_container{
    margin-top: -4rem;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;
    padding-bottom: 3rem;
}
.plans_header{
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.plans_header>span:nth-of-type(2){
    color: white;
    font-size: 3rem;
    font-weight: bold;
}
.plans{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.plan{
    display: flex;
    flex-direction: column;
    background-color: var(--caloryCard);
    color: white;
    gap: 2rem;
    padding: 1.5rem;
    width: 15rem;
}
.plan:nth-child(2){
    background-color: var(--orange);
    transform: scale(1.1);
}
.plan>svg{
    fill: var(--orange);
    width: 2rem;
    height: 2rem;
}
.plan:nth-child(2)>svg{
    fill: rgb(19, 19, 19);
    width: 2rem;
    height: 2rem;
}
.plan>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
}
.plan>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;
}
.features{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.feature{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.feature>img{
    width: 1rem;
}
@media screen and (max-width: 1024px){
    
}
@media screen and (max-width: 600px){
    .plans_header{
        display: flex;
        flex-direction: column;
    }
    .plans_header>span:nth-of-type(2){
        font-size: 2rem;
    }
    .plans_header>.stroke-text{
        font-size: 1.7rem;
    }
    .plans{
        gap: 4rem;
        flex-direction: column;
    }
    .plan{
        padding: 1.5rem;
        width: 50vw;
    }
}